<template>
  <div class="zhuye">
    <div class="actorcontent ">
      <div class="actorItem clear" v-for="item in actor">
        <div class="actorTop">
          <img :src="item.autor" class="autor">
          <span class="autorname">
                <p class="aname"><span>{{item.name}}</span><span class="loction">深圳</span><span class="loction">web前端</span></p>
                <p class="atime"><span style="color: #2196f3">{{item.title}}</span> {{item.creatTime}}</p>
              </span>
          <span style="float: right"><a :href="item.href" target="_blank" v-if="item.href">原文链接</a></span>
        </div>
        <div class="actorbody">
          <div v-html="item.content" class="actorcon"></div>
          <div class="imgs">
                   <span v-for="img in item.imags" class="imgitem">
                     <img :src="img">
                   </span>
          </div>
        </div>

        <div class="actorfooter"> <a :href="path+item.fromhref" target="_blank">{{item.fromhreftxt}}</a></div>
      </div>
    </div>
  </div>
</template>


<script>
  import photo from "@/assets/photo.png"
  import project171023a from "@/assets/actorImgs/project/canvas/gobang.png"

    export default {
    name: 'HelloWorld',
    data () {
      return {
        actor:[{
          autor:photo,
          name:"无疑",
          creatTime:"2017-10-23",
          title:"简易五子棋" ,
          content:"不到100行的代码实现简易的五子棋，也是对canvas的基本用法实践，写字、画线、闭合路径、填充，五子棋的AI部分暂时未实现，后期可能会结合socketIO,写一个联网五子棋！",
          imags:[project171023a],
          href:"",
          fromhreftxt:"演示地址",
          fromhref:"static/project/canvas/Gobang.html"
        },

        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .actorItem{
    width: 100%;
    background-color: #fff;
    padding: 16px;
    box-sizing: border-box;
    border-radius: 2px;
    margin-bottom: 20px;
  }
  .autor{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: solid 1px #eee;
    display: inline-block;
  }
  .autorname{
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
    line-height: 25px;
  }
  .autorname .aname{
    font-size:16px ;
    font-weight: 600;
  }
  .loction{
    margin-left: 15px;
    background-color: #FFB800;
    border-radius: 5px;
    font-size: 12px;
    padding:3px 5px;
    font-weight: 100;
    color: #fff;
  }
  .actorcon{
    font-size: 14px;
    text-indent: 30px;

  }
  .imgs{
    max-height: 160px;
    overflow: hidden;
    margin: 10px 20px;
  }
  .imgitem{
    overflow: hidden;
    position: relative;
    float: left;
    width: 33.33%;
    box-sizing: border-box;
    padding: 0 10px 20px;
  }
  .imgitem img{
    width: 100%;
    float: left;
    height: auto;
    border-radius: 0;
    /*cursor: url(../images/open_new.cur), auto;*/
    border: 1px solid #f1f1f1;
  }
  .actorfooter{
    margin-top: 10px;
    padding-top: 16px;
    border-top: 1px solid #f1f1f1;
    line-height:30px;
    text-align: right;
  }
</style>



